export default {
	data() {
		return {
			user: {
				workunit:'单位',
				workplace:'地址',
				workstation:'职位',
				workindustry:'行业',
				workprovince:'省份'
			}
		};
	},
	methods: {
		save: function() {
			// console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
			var self = this;
			// data.bkyuanxi = self.yuanxiarray[self.index].ID;
			uni.request({
				url: self.$url + 'updateinfo/updateworkinfo',
				data: self.user,
				method: 'POST',
				header: {
					token: global.islogin()
				},
				success: function(res) {
					if (res.data.code == 1) {
						uni.showModal({
							title: res.data.msg,
							// content: '这是一个模态弹窗',
							success: function(res) {
								if (res.confirm) {
									uni.reLaunch({
										url: '../../tabBar/user/user'
									});
								}
							}
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							icon: 'none'
						});
					}
				}
			});
		},
	},
	onLoad() {
		var self=this;
		//获取工作信息
		uni.request({
			url: self.$url + 'Userinfo/getWorkinfo',
			method: 'GET',
			header: {
				token: global.islogin()
			},
			success: function(res) {
				// console.log(res.data.data.zuigaoxueli);
				if(res.data.data.work){
					self.user=res.data.data.work;
				}
			}
		});
	}
};
